$(function () {

	const headerMain = $('.header-main');
	const headerNav = $('.header__nav');
	const menuBtn = $('.menu__btn');
	const testimonialsSlider = $('.testimonials__slider');

	let scrollOffset = 0;
	let scrollAmount = 0;
	$(window).on("scroll", function () {
		let scrollOffset = $(this).scrollTop();

		if ($(this).innerWidth() > 768) {
			headerOnScroll();
		}

		function headerOnScroll() {
			if (scrollOffset >= scrollAmount) {
				scrollAmount = scrollOffset;
				headerMain.addClass('scroll');
			} else {
				scrollAmount = scrollOffset;
				headerMain.removeClass('scroll');
			}
		}
	});

	menuBtn.click(function(event) {
		$(this).toggleClass('active');
		headerNav.toggleClass('active');
	});

	if (testimonialsSlider.length > 0) {
		testimonialsSlider.slick({
		  arrows: false,
		  dots: true,
		  infinite: true,
		  autoplay: true,
	      autoplaySpeed: 5000,
		  speed: 300,
		  slidesToShow: 1,
		  slidesToScroll: 1
		});
	}

	$('.contacts-form').on('mouseup', function(event) {
		
	});


})
